
li{
	list-style: none;
	height: 40px;
	font: 20px/2 "微软雅黑";
}

/*li{
	
}*/
#box{
	overflow: hidden;
}
.searchInp{
	height: 50px;
	width: 500px;
	text-indent: 20px;
	outline: none;
	font: 20px/50 "微软雅黑";
}
.searchContent{
	width: 500px;
	margin: 0 auto;
}
.searchContent ul{
	padding: 0;
}
.searchContent li{
	-webkit-transition: 0.4s;
	transition: 0.4s;
	text-align: left;
	margin-bottom: 10px;
	background-color: pink;
}
.searchContent .active{
	background-color: orangered;
	color: #fff;
}
.searchContent .hover{
	background-color: yellowgreen;
	color: #fff;
}
.v-enter{
	-webkit-transform: scale(2);
	        transform: scale(2);
}
.itemfade-enter,.itemfade-leave-active {
    opacity: 0;
    -webkit-transition: .4s .4s;
    transition: .4s .4s;
}
.itemfade-leave-active {
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    height:0px
}




